.profile {
    flex-basis: 0;
    flex-grow: 1;
    border: 1px solid black;
    height: 100%;
}

.profile_top {
    height: 50vh;
}

.profile_top_top {
    height: 50%;
}

.profile_background_img {
    height: 100%;
    background-color: #CFD9DE;
}

.profile_img_wrapper1 {
    position: relative;
    height: 100%;
}

.profile_img_wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 16vh;
    width: 16vh;
    position: absolute;
    z-index: 0;
    top:17vh;
    margin-left: 2vh;
    background-color: transparent;
}

.profile_img_circle {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 90%;
    height: 90%;
    background-color: #CFD9DE;
    border-radius: 50%;
    border: 4px solid white;
}

.profile_top_bottom {
    height: 50%;
    display: flex;
    flex-direction: column;
}

.profile_edit_wrapper {
    display: flex;
    height: 35%;
    justify-content: flex-end;
}

.profile_edit {
    display: flex;
    width: 35%;
    align-items: center;
    justify-content: flex-end;
}

.profile_edit > span {
    padding: 1vh;
    width: 50%;
    border: 1px solid gray;
    border-radius: 50vh;
    font-size: 1.7vh;
    font-weight: 600;
    color: rgb(29, 29, 29);
    text-align: center;
    margin-right: 1vh;
    cursor: pointer;
}

.edit_profile_modal_wrapper {
    z-index: 10;
    position: fixed;
    top:0; left: 0; bottom: 0; right: 0;
    background: rgba(0, 0, 0, 0.8);
}

.edit_profile_modal {
    display: flex;
    width: 45vh;
    height: 58vh;
    background-color: teal;
    position: fixed;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
    -moz-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    -o-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
}

.profile_info_name, .profile_info_state, .profile_info_creationtime {
    display: flex;
    align-items: center;
}

.profile_info_name {
    height: 15%;
    background-color: rgb(248, 195, 248);
    font-weight: 600;
    font-size: 2vh;
}

.profile_info_state {
    height: 15%;
    background-color: rgb(59, 240, 3);
}

.profile_info_creationtime {
    height: 15%;
    background-color: rgb(255, 165, 106);
    color: rgb(93, 93, 93);
}

.profile_menu_bar {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex: 1;
}

.profile_menu_bar > span {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    width: 50%;
    position: relative;
}

.profile_menu_bar > span:hover {
    background-color: gray;
    text-decoration:none; 
    border-bottom:3px solid #00acee;
}

.profile_bottom {
    height: 50vh;
    background-color: purple;
}

.profile_info_name > span, .profile_info_state > span, .profile_info_creationtime > span {
    margin-left: 1.5vh;
}